<template>
    <div class='statistics'>
        <div class='title'>课程统计</div>
        <div class='firchart'>
            <div class='text'>
                <div class='order'>预约订单</div>
                 <el-select v-model="value6" placeholder="请选择" @change="getDate($event, 'order')">
                    <el-option
                    v-for="item in times"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    {{item.label}}
                    <img src='../../assets/images/icon_right.png'/>
                    </el-option>
                </el-select>
            </div>
            <div id='myChart' :style="{width: '100%', height: '5.74rem'}" v-show='!mychart'>

            </div>
            <div v-show='mychart' class='notext'>暂无数据</div>
        </div>
        <div class='firchart secchart'>
            <div class='text'>
                <div class='order'>课时量</div>
                 <el-select v-model="value7" placeholder="请选择" @change="getDate($event, 'sum')">
                    <el-option
                    v-for="item in times"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    {{item.label}}
                    <img src='../../assets/images/icon_right.png'/>
                    </el-option>
                </el-select>
            </div>
            <div id='myChartSum' :style="{width: '100%', height: '5.74rem'}" v-show='!myChartSum'>

            </div>
            <div v-show='myChartSum' class='notext'>暂无数据</div>
        </div>
    </div>
</template>
<script>
// 引入基本模板
let echarts = require('echarts/lib/echarts')
// 引入柱状图组件
require('echarts/lib/chart/line')
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
// 引入legend模块
require("echarts/lib/component/legend")
import {statistics} from '@/services/service'
import {getStatDate, isApiResponseOk, getDates, getMonthDate, transDate} from '@/utils/com'
import { sep } from 'path';
export default {
    data () {
        return {
            times: [
                {label: '过去7天', value: '-7'},
                {label: '过去14天', value: '-14'},
                {label: '过去30天', value: '-30'},
                {label: '本周', value: 'week'},
                {label: '本月', value: 'month'}
            ],
            value6: '过去14天',
            value7: '过去14天',
            mychart: false,
            myChartSum: false,
            reserveNumData: [], // 预约订单
            reserveNumList: [] // 预约的人数
        }
    },
    created () {
        // 
        console.log(getMonthDate())
    },
    mounted () {
        this.initInfo()
    },
    methods: {
        drawLine (str) {
            // 基于准备好的dom,初始化echarts实例
            let myChart = echarts.init(document.getElementById(str))
            // 绘制图表
            myChart.setOption({
                title: { text: '' },
                tooltip: {
                    trigger: 'axis'
                },
                legend: { // 右边的标注
                    orient: 'vertical', // 'vertical'
                    x: '93%', // 'center' | 'left' | {number},
                    y: '70%', // 'center' | 'bottom' | {number}
                    // backgroundColor: '#eee',
                    // borderColor: 'rgba(178,34,34,0.8)',
                    // borderWidth: 4,
                    //padding: 10,    // [5, 10, 15, 20]
                    itemGap: 20,
                    itemWidth: 18, //线的长度
                    itemHeight: 6, // 圆的直径
                    textStyle: {color: '#8597A1', fontSize: 11},
                    data: [
                        '私教课','团课', '特色课', '总计'
                    ]
                },
                grid: {
                    top: '37',
                    left: '50',
                    right: '200',
                    bottom: '54'
                },
                calculable : true,
                xAxis: {
                    data: this.reserveNumData,
                    axisLine:{
                        lineStyle:{
                            color:'#d8d8d8',
                        },
                        textStyle: {
                            color: 'rgba(0,0,0,0.65)',
                            fontSize : 14
                        }
                    }
                },
                yAxis: {
                    axisLine:{       //y轴
                        show: false,
                        textStyle: {
                            color: 'red',
                            fontSize : 14
                        }
                    },
                    axisTick:{       //y轴刻度线
                        show:false
                    },
                    splitLine: { // 网格线样式
                        show: true,
                        lineStyle:{
                            color: ['#E8E8E8'],
                            width: 1,
                            type: 'dashed'
                        }
                    },
                },
                series: [{
                    name: '私教课',
                    type: 'line',
                    //stack: '总量',
                    symbol: "none",
                    data: this.reserveNumList[0],
                    itemStyle: { // 修改折线颜色
                        normal: {
                            color: "#20C6BA",
                            lineStyle: {
                                color: "#20C6BA"
                            }
                        }
                    },
                },
                {
                    name: '团课',
                    type: 'line',
                    //stack: '总量',
                    symbol: "none",
                    data: this.reserveNumList[1],
                    itemStyle: { // 修改折线颜色
                        normal: {
                            color: "#FC7563",
                            lineStyle: {
                                color: "#FC7563"
                            }
                        }
                    },
                },
                {
                    name: '特色课',
                    type: 'line',
                    //stack: '总量',
                    symbol: "none",
                    data: this.reserveNumList[2],
                    itemStyle: { // 修改折线颜色
                        normal: {
                            color: "#4140D8",
                            lineStyle: {
                                color: "#4140D8"
                            }
                        }
                    },
                },
                {
                    name: '总计',
                    type: 'line',
                    //stack: '总量',
                    symbol: "none",
                    data: this.reserveNumList[3],
                    itemStyle: { // 修改折线颜色
                        normal: {
                            color: "#263238",
                            lineStyle: {
                                color: "#263238"
                            }
                        }
                    },
                }]
            })
        },
        async initInfo (obj, str) {
            let self =  this
            if (!obj) {
                obj = getStatDate(-14)
            }
            const res =  await statistics(obj.beiginTime, obj.endTime)
            if (isApiResponseOk(res) && res.data) {
                if (str === 'order' || !str) {
                    let privateCourseReserveNum = res.data.privateCourseReserveNum ? res.data.privateCourseReserveNum : [] // 私教课预约人数
                    self.getArray(privateCourseReserveNum, 0)
                    let groupCourseReseveNum = res.data.groupCourseReserveNum ? res.data.groupCourseReserveNum : [] // 团课预约人数
                    self.getArray(groupCourseReseveNum, 1)
                    let specialCourseReserveNum = res.data.specialCourseReserveNum ? res.data.specialCourseReserveNum : [] // 特色课预约人数
                    self.getArray(specialCourseReserveNum, 2)
                    let totalCourseReserveNum = res.data.totalCourseReserveNum ? res.data.totalCourseReserveNum : []// 总预约人数
                    self.getArray(totalCourseReserveNum, 3)
                    self.drawLine('myChart');
                    self.mychart  = false
                } 
                if (str === 'sum' || !str) {
                    let privateOrderNum = res.data.privateOrderNum ? res.data.privateOrderNum : []// 私教课订单数
                    self.getArray(privateOrderNum, 0)
                    let groupOrderNum = res.data.groupOrderNum ? res.data.groupOrderNum : [] // 团课订单人数
                    self.getArray(groupOrderNum, 1)
                    let specialOrderNum = res.data.specialOrderNum ? res.data.specialOrderNum : []// 特色课订单数
                    self.getArray(specialOrderNum, 2)
                    let totalOrderNum = res.data.totalOrderNum ? res.data.totalOrderNum : []// 总订单数
                    self.getArray(totalOrderNum, 3)
                    self.drawLine('myChartSum');
                    self.myChartSum  = false
                }
            } else {
                if (!str) {
                    self.mychart  = true
                    self.myChartSum  = true
                } else if (str === 'order') {
                    self.mychart  = true
                } else if (str === 'sum') {
                    self.myChartSum  = true
                }
            }
        },
        getArray (arr, seq) {
            if (!Array.isArray(arr)) return
            let self = this
            self.reserveNumList[seq] = []
            self.reserveNumData = []
            arr.forEach(item => {
                item.showDay = transDate(item.showDay)
                self.reserveNumData.push(item.showDay)
                self.reserveNumList[seq].push(item.count)
            })
        },
        // 获取时间
        getDate (data, str) {
            let obj = {}
            let date = new Date()
            let month = date.getMonth() + 1
            let year = date.getFullYear()
            let day = date.getDate()
            let time = year + '/' + month + '/' + day
            if (data === 'week') {
                let list = getDates(time)
                list[0] = list[0].replace('-', '/')
                list[6] = list[0].replace('-', '/')
                obj.beiginTime = new Date(list[0] + ' ' + '00:00:00').getTime()
                // 要获取下周一的日期
                let date = new Date (list[6])
                date.setDate(date.getDate() + 1)
                obj.endTime = date.getTime()
            } else if (data === 'month') {
                obj = getMonthDate()
            } else {
                obj = getStatDate(data)
            }
            this.initInfo(obj, str)
        }
    }
}
</script>
<style lang="scss">
.statistics{
    .title{
        font-family: PingFangSC-Semibold;
        font-size: .24rem;
        color: #263238;
        letter-spacing: 0;
        line-height: .24rem;
        margin-bottom: .14rem;
    }
    .firchart{
        width: 100%;
        background: #fff;
        .text{
            height: .61rem;
            line-height: .61rem;
            padding-left: .3rem;
            padding-right: .2rem;
            border-bottom: 1px solid #E8E8E8;
            display: flex;
            .order{
                flex: 1;
            }
            .el-select{
                width: 1.27rem;
                .el-input__inner{
                    height: .29rem;
                    line-height: .29rem;
                }
            }
        }
        .notext{
            text-align: center;
            min-height: 5.74rem;
            display: flex;
            align-items: center;
            justify-content: center;
            line-height: 5.74rem;
        }
        .myChart{
            div{
                width: 100% !important;
            }
            canvas{
                width: 100% !important;
            }
        }
    }
    .secchart{
        margin-top: .2rem;
    }
}
</style>





